___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --color-background: rgb(40, 52, 95);
  --color-backgrund-container: #ffffffea;

  --font-family: "Readex Pro";
}

* {
  box-sizing: border-box;
  outline: none;
}

html {
  font-family: var(--font-family), sans-serif;
  font-size: 1.2em;
  line-height: 1.5em;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--color-background);
}

h1 {
  display: inline-block;
  width: auto;
  background-image: linear-gradient(to right, rgb(0, 0, 255) 1%, rgb(255, 174, 0));
  color: transparent;
  background-clip: text;
}

.container {
  max-width: 640px;
  width: max-content;
  margin: 15px auto;
  background-color: var(--color-backgrund-container);
  padding: 20px;
  border-radius: 10px;
}

form input,
form label,
form button {
  display: block;
  width: 100%;
}

form input {
  margin-bottom: 10px;
  font-size: 16px;
  height: 40px;
  padding: 10px;
  border-radius: 5px;
}

form input:focus {
  outline: 2px solid var(--color-background);
}

form button {
  border: none;
  background-color: var(--color-background);
  padding: 10px;
  color: white;
  cursor: pointer;
  border-radius: 5px;
}

form button:hover {
  outline: 2px solid white;
}

.form-senha label {
  display: block;
  font-size: 1rem;
  color: var(--color-background);
}


.btn-gerar-senha {
  margin-top: 30px;
  padding: 10px;
  font-size: 1rem;
  border: 2px solid var(--color-background);
  border-radius: 10px;
}
